<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.account" placeholder="学号">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')">查找</Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>

    <Modal
      v-model="modal1"
      title="确认提醒"
      @on-ok="remove(params.index)">
      <div style="text-align:center">
        <p>确认不通过该用户的审核申请吗？</p>
      </div>
    </Modal>

    <Modal
      v-model="modal2"
      title="确认提醒"
      @on-ok="ok(params.index)">
      <div style="text-align:center">
        <p>确认通过该用户的审核申请吗？</p>
      </div>
    </Modal>

  </div>
</template>

<script type="es6">
  export default {
    name: 'VerifyUser',
    data () {
      return {
        //初始化
        total: '',
        condi: '',
        modal1: false,
        formInline: {
          account: ''
        },
        formItem2: {
          account: '',
          name: '',
          sex: '男',
          condi: '0',
          number:'',
          email:'',
        },
        ruleItem2: {
          account: [{
            required: true,
            message: '请填写账号！',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '请填写学生姓名',
            trigger: 'blur'
          }]
        },
        columns7: [
          {
            title: '学号',
            key: 'account',
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'person'
                  }
                }),
                h('strong', params.row.account)
              ]);
            }
          },
          {
            title: '姓名',
            key: 'name'
          },
          {
            title: '性别',
            key: 'sex'
          },

          {
            title: '身份',
            key: 'condi'
          },

          {
            title: '操作',
            key: 'action',
            width: 300,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                //查看详细信息
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, '查看'),

                //通过审核并删除待审核记录
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                      //成功在数据库添加该用户，显示成功通过
                      this.showSucess()
                    }
                  }
                }, '通过'),

                //不通过审核并且删除该记录
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      //显示不通过的确认提示
                      this.modal1=true
                      //确认不通过后在待审核人员名单中清除
                      this.remove(params.index)
                    }
                  }
                }, '不通过')

              ]);
            }
          }
        ],

        //修改过个人信息的提交了审核申请的部分数据
        data6: [
          {
            account:'123456',
            name:'王小明',
            sex:'男',
            condi:'学生',
            number:'123456789',
            email:'123456@qq.com',

          },
          {
            account:'123456',
            name:'小李',
            sex:'女',
            condi:'学生',
            number:'123456789',
            email:'123456@qq.com',

          }
        ]
      }
    },
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },

      //操作键显示用户信息
      show (index) {
        this.$Modal.info({
          title: '用户信息',
          content: `姓名：${this.data6[index].name}
          <br>年龄：${this.data6[index].sex}
          <br>学号：${this.data6[index].account}
          <br>身份：${this.data6[index].condi}
          <br>电话号码: ${this.data6[index].number}
          <br>邮箱：${this.data6[index].email}`

        })
      },
      showSucess(){
        this.$Modal.info({
          title: '系统提示',
          content: `成功通过`
        })
      },


      //删除键删除用户信息
      remove (index) {
        this.data6.splice(index, 1);
      },

      //请求页面
      request (currentPage){
        var that=this
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllReaders',
          {
            account: that.formInline.account,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=res.data.readers
        }).catch((e) => {
          this.$Message.fail('网络有误！')
        })
      },
      changePage: function(page){
        this.request(page)
      },

      //通过用户的审核申请
      ok (name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addReader',
              {
                account: that.formItem.account,
                name: that.formItem.name,
                sex: that.formItem.sex,
                condi: that.formItem.condi,
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                that.$Message.success('新增成功')
                that.formInline.account=''
                that.request(1)
              }else{
                that.$Message.error('已存在该学号的用户')
              }

            }).catch((e) => {
              that.$Message.fail('网络有误！')
            })
          }
        })
      }
    }
  }
</script>




