<template>
  <div class="container">
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>
  </div>
</template>
<script type="es6">
  export default {
    name: 'MyRecord',
    data () {
      return {
        total: '',
        condi: '',
        modal1: false,
        modal2: false,
        currIndex: 0,//最近被点击添加编号副本的图书编号
        formInline: {
          raccount: ''
        },
        //列的信息
        columns7: [
          {
            title: '编号',
            key: 'bid',
            //嵌套元素,显示编号一栏加了一个标签
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'document-text'
                  }
                }),
                //编号的实际值在这里:
                h('strong', params.row.bid)
              ]);
            }
          },
          //其他属性
          {
            title: '书名',
            key: 'title'
          },
          {
            title: '编号',
            key: 'number'
          },
          {
            title: '借阅者学号',
            key: 'raccount'
          },
          {
            title: '借阅者姓名',
            key: 'rname'
          },
          {
            title: '借阅时间',
            key: 'time'
          },
          {
            title: '应归还时间',
            key: 'backtime'
          },
          {
            title: '是否超期',
            key: 'condi'
          }
        ],
        data6: [],
        data7: [] //存放从后台请求过来的借阅记录
      }
    },
    //mounted:编译好的HTML挂载到页面完成后执行的事件钩子，此钩子函数中一般会做一些ajax请求获取数据进行初始化。
    mounted(){
      this.request(1);
    },
    methods: {
      request (currentPage){ //请求当前页面
        var that=this
        //基本用法 $http.post(url + data + functioin)
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllBorrowRecords',
          {
            raccount: window.localStorage.getItem('account'),
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.borrowrecords)
          that.total=res.data.pageInfo.total
          that.data6=[]
          that.data7=res.data.borrowrecords
          //数组循环遍历，data7从本地获取了数据的数组，obj作为空数组，
          //遍历data7的每个元素e的每个属性，赋值给obj,然后把obj push到data6里面
          that.data7.forEach((e) =>{
            let obj = {}
            obj.bid = e.bid
            obj.title = e.album.title
            obj.number = e.subalbum.number
            obj.raccount = e.raccount
            obj.rname = e.reader.name
            obj.time = e.time
            obj.backtime = e.backtime
            var time = new Date().getTime();
            console.log(time)
            if(time < e.inttime){
              obj.condi = '未超期'
            }else{
              var delayDay = Math.ceil((time - e.inttime)/86400000)
              obj.condi = '超期'+delayDay+'天'
            }
            that.data6.push(obj)
          })
        })
      },
      changePage: function(page){
        this.request(page)
      },
    }
  }
</script>




