<template>
  <div class="container">
    <Form ref="formItem" :model="formItem" :rules="ruleItem" :label-width="80">
      <Row>
        <Col :xs="23" :sm="23" :md="23" :lg="23">
        <h1>{{msg}}</h1>
        </Col>
      </Row>

      <Row>
        <Col :xs="10" :sm="10" :md="10" :lg="10">
        <div style="font-size:16px;">
          <p id="verifyCode">姓名：{{name}}</p>
        </div>
        </Col>
      </Row>
      <br>

      <Row>
        <Col :xs="10" :sm="10" :md="10" :lg="10">
        <div style="font-size:16px;">
          <p id="verifyCode">学号：{{account}}</p>
        </div>
        </Col>
      </Row>
      <br>

      <Row>
        <Col :xs="10" :sm="10" :md="10" :lg="10">
        <div style="font-size:16px;">
          <p id="verifyCode">电话：{{phonenumber}}</p>
        </div>
        </Col>
        <Button id="login_btn" size="small" type="primary" @click="modal1 = true">修改</Button>
      </Row>
      <br>

      <Row>
        <Col :xs="10" :sm="10" :md="10" :lg="10">
        <div style="font-size:16px;">
          <p id="verifyCode">邮箱：{{email}}</p>
        </div>
        </Col>
        <Button id="login_btn" size="small" type="primary" @click="modal2 = true">修改</Button>
      </Row>
      <br><br><br>

      <Row>
        <Col :xs="30" :sm="30" :md="30" :lg="30" >
        <div>
          <Button id="login_btn" type="primary" @click="modal3 = true">修改密码</Button>
          <Button id="login_btn" type="success" @click="handleSubmit('formItem')">提交修改</Button>
        </div>
        </Col>
      </Row>

    </Form>

    <Modal
      v-model="modal1"
      title="修改电话号码"
      width="800"
      scrollable="true"
      ok-text="保存"
      @on-ok="ok2('formItem2')"   >
      <Form ref="formItem2" :model="formItem2" :rules="ruleItem" :label-width="85">
        <FormItem label="电话号码:" prop="phonenumber">
          <Input style="width: 200px" v-model="formItem2.phonenumber" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>

    <Modal
      v-model="modal2"
      title="修改邮箱"
      width="800"
      scrollable="true"
      ok-text="保存"
      @on-ok="ok3('formItem3')"   >
      <Form ref="formItem3" :model="formItem3" :rules="ruleItem" :label-width="85">
        <FormItem label="邮箱:" prop="email">
          <Input style="width: 200px" v-model="formItem3.email" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>

    <Modal
      v-model="modal3"
      title="修改密码"
      width="800"
      scrollable="true"
      ok-text="保存"
      @on-ok="ok4('formItem4')"   >
      <Form ref="formItem4" :model="formItem4" :rules="ruleItem" :label-width="85">
        <FormItem label="密码:" prop="password">
          <Input style="width: 200px" v-model="formItem4.password" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>

  </div>
</template>

<script>
  export default {
    name: 'Individual',
    data () {
      return {
        msg: '个人中心',
        //初始化
        modal1: false,
        modal2: false,
        modal3: false,
        name:'',
        account: '',
        phonenumber:'',
        email:'',
        password:'',
        content:'',

        formItem: {
          name:'',
          account: '',
          phonenumber:'',
          email:'',
          password: ''
        },
        formItem2: {
          phonenumber:'',
        },
        formItem3: {
          email:'',
        },
        formItem4: {
          password:'',
        },


        ruleItem: {
          account: [{
            required: true,
            message: '请填写账号！',
            trigger: 'blur'
          }],
          password: [{
            required: true,
            message: '请填写密码',
            trigger: 'blur'
          }, {
            type: 'string',
            min: 3,
            message: '密码长度不能小于6位',
            trigger: 'blur'
          }]
        },
        loading: false
      }
    },
    mounted () {
      this.request(1);
    },
    //使用vuex获取当前用户的信息，可能需要的js代码在store.js
    computed: {
      user() {
        return this.$store.state.user;
      }
    },
    methods: {
      //获取当前用户信息的方法
      getName(){
        name= window.localStorage.getItem('username', res.data.loginUser.name);
        return name;
      },
      getAccount(){
        account= window.localStorage.getItem('account', res.data.loginUser.account);
        return account;
      },
      getPhoneno(){
        phonenumber= window.localStorage.getItem('phonenumber', res.data.loginUser.phonenumber);
        return phonenumber;
      },
      getEmail(){
        email= window.localStorage.getItem('email', res.data.loginUser.email);
        return email;
      },
      //修改数据库并显示提交修改后的结果
      handleSubmit (name) {
        var that = this;
        this.loading = true;
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/login',
              {
                //向服务器提交要处理的数据
                name: that.formItem.name,
                account: that.formItem.account,
                phonenumber: that.formItem2.phonenumber,
                email: that.formItem3.email,
                password: that.formItem4.password
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.loginUser);
              if (res.data.result === 'yes') {
                this.$Message.success('修改提交成功!'),
                  //保存网站的数据
                  window.localStorage.setItem('email', res.data.loginUser.email);
                window.localStorage.setItem('phonenumber', res.data.loginUser.phonenumber);
                window.localStorage.setItem('password', res.data.loginUser.password);
              } else {
                this.$Message.error('修改提交失败！请重新修改！')
                this.loading = false
              }
            })

          } else {
            this.loading = false
          }
        })
      },
      changePage(){
        this.$Loading.start();
        this.$Loading.finish();
        this.$router.push({path: '/'});
      },

//处理修改事件的按钮的触发事件
      ok2(name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addAlbum',
              {
                phonenumber: that.formItem2.phonenumber
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                //保存电话号码修改的通知
                that.$Notice.config({
                  top: 50,
                  duration: 3,
                  title: '通知',
                  desc: '已经保存修改！请点击提交修改！'
                })

                that.formItem2.phonenumber=''
                that.request(1)
              }

            }).catch((e) => {
              that.$Message.fail('网络有误！')
            })
          }
        })
      },

      ok3(name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addAlbum',
              {
                email: that.formItem3.email
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                //保存邮箱的修改的通知
                that.$Notice.config({
                  top: 50,
                  duration: 3,
                  title: '通知',
                  desc: '已经保存修改！请点击提交修改！'
                })

                that.formItem3.email=''
                that.request(1)
              }

            }).catch((e) => {
              that.$Message.fail('网络有误！')
            })
          }
        })
      },

      ok4(name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addAlbum',
              {
                password: that.formItem4.password
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                //保存密码修改的通知
                that.$Notice.config({
                  top: 50,
                  duration: 3,
                  title: '通知',
                  desc: '已经保存修改！请点击提交修改！'
                })

                that.formItem4.password=''
                that.request(1)
              }

            }).catch((e) => {
              that.$Message.fail('网络有误！')
            })
          }
        })
      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .individual{
    margin: auto;
  }
  h1{
    color:#2D8CF0;
    margin-bottom:30px;
  }
  Form{
    margin:0 auto;
    width:500px;
    border: 2px solid grey;
    padding:50px 0;
    /*background-color:red;*/
  }


</style>



