<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.account" placeholder="学号">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')">查找</Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>
  </div>
</template>
<script type="es6">
  export default {
    name: 'Feedback',
    data () {
      return {
        total: '',
        condi: '',
        formInline: {
          account: ''
        },
        columns7: [
          {
            title: '学号',
            key: 'account',
            //嵌套元素,显示编号一栏加了一个图标
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'person'
                  }
                }),
                //学号的值在这里：
                h('strong', params.row.account)
              ]);
            }
          },
          {
            title: '姓名',
            key: 'name'
          },
          {
            title: '性别',
            key: 'sex'
          },
          {
            title: '身份',
            key: 'condi'
          },
          {
            title: '邮箱',
            key: 'email'
          },
          {
            title: '用户留言',
            key:'feedback'
          },
          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                //嵌套元素,显示操作一栏加了一个按钮
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                        //触发remove()删除信息
                        this.remove(params.index);
                      this.$Message.info('用户留言已删除',3);
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        data6: [
          {
            account:'201830681053',
            name:'小刘',
            sex:'女',
            condi:'学生',
            email:'123456789@qq.com',
            feedback:'此图书管理系统简洁实用，但书籍种类太少，读者可借天数太短，希望改进'
          }
        ]
      }
    },
    //mounted:编译好的HTML挂载到页面完成后执行的事件钩子，此钩子函数中一般会做一些ajax请求获取数据进行初始化。
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },
      show (index) {
        if(this.data6[index].condi==0){
          this.condi='学生'
        }else{
          this.condi='图书管理员'
        }
        this.$Modal.info({
          title: '用户信息',
          content: `学号：${this.data6[index].account}<br>姓名：${this.data6[index].name}<br>性别：${this.data6[index].sex}<br>身份：${this.condi}<br>邮箱：${this.data6[index].email}<br>用户留言：${this.data6[index].feedback}`
        })
      },
      remove (index) {
        //删除元素
        this.data6.splice(index, 1);
      },
      request (currentPage){
        var that=this
        //基本用法 $http.post(url + data + functioin)
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllReaders',
          {
            account: that.formInline.account,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=res.data.readers
        }).catch((e) => {
          this.$Message.fail('网络有误！')
        })
      },
      changePage: function(page){
        this.request(page)
      }
    }
  }
</script>




