<template>
  <div class="layout">
    <Modal v-model="modal6" width="360">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>退出确认</span>
      </p>
      <div style="text-align:center">
        <p>退出后，将返回登录界面</p>
        <p>是否退出？</p>
      </div>
      <div slot="footer">
        <i-button type="error" size="large" style="color:white" long :loading="modal_loading" @click="del">退出</i-button>
      </div>
    </Modal>

    <Modal
      v-model="modal2"
      title="留言反馈"
      @on-ok="ok"
      @on-cancel="cancel">
      <p>评分：<Rate show-text :value.sync="value"></Rate></p>
      <i-input type="textarea" :rows="4" placeholder="请输入..."></i-input>
    </Modal>

    <Modal v-model="modal7" width="600">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span>帮助中心</span>
      </p>
      <div>
        <p>亲爱的读者：</p>
        <p> &nbsp;&nbsp;&nbsp;&nbsp;欢迎使用此图书借阅管理系统。为了让您和他人有更好的阅读体验，请仔细阅读以下事项。</p>
        <br>
        <h4>一、借阅规则：</h4>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;1. 本图书馆所有书籍对xx大学所有学生和教职人员开放。读者初次使用本系统需凭学号或教职工号注册，登录后则可随时修改个人信息与账号密码。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;2. 读者可以在图书馆检索系统浏览和查询数目和借阅状态，可在借阅记录中查看个人借阅记录。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;3. 每位读者可借阅5本书籍，借期60天。需要续借的读者请先归还再继续按照流程借阅。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;4. 读者须在借阅期限到达之前归还书籍，逾期将按相关规定收取超期滞纳金，每天每本0.1元。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;5. 确认借阅图书时，读者应当当场确认图书是否有损坏，如有问题及时向工作人员反映。一旦确认借阅后，衍生的损毁问题，由读者自行负责。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;6. 读者外借图书时，读者应当确认图书是否有损坏，否则衍生损毁问题，同样又读者自行负责。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;7. 读者归还图书时，应当到图书馆归还，并确认归还至指定的归还地点。否则管理员无法登记归还。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;8. 图书有损坏、遗失处理按本规定第二条遗失赔偿办法中的相关规定执行。</p>
        <br>
        <h4>二、损坏遗失赔偿办法</h4>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;1. 损坏赔偿。读者若在使用过程中对图书造成损坏，应视损坏程度轻重予以相应赔偿，赔偿范围按照著录价格的1-2倍收取。损坏严重导致不能正常使用或影响使用的，按第2款“遗失赔偿”条款处理。</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;2. 遗失赔偿。读者若遗失图书，需及时向图书馆报告，并购买相同的书本赔偿，或者按照著录价格的2倍赔偿。</p>
        <br>
        <p>如有疑问或遇到问题，请点击系统右上角【留言反馈】给管理员留言，管理员会一一处理并通过邮件与你联系。</p>
        <br>
        <div style="text-align:right">
          <p>xx大学图书管理系统</p>
        </div>
      </div>
      <div slot="footer">
        <i-button type="error" size="large" style="color:white" long :loading="modal_loading" @click="confirm()">确定</i-button>
      </div>
    </Modal>

    <div class="layout-ceiling">
      <div class="layout-ceiling-left">
        <div class="layout-logo-left">
          <h2 style="color:white;">图书管理系统</h2>
        </div>
      </div>
      <div class="layout-ceiling-main">
        <a href="#"> <Icon type="ios-browsers-outline"></Icon></a>
        <a @click="modal7 = true">帮助中心</a> |
        <a @click="modal2 = true">留言反馈</a> |
         <a  @click="modal6 = true">退出登录</a>
      </div>
    </div>
    <div class="layout-header">
      <div class="layout-logo"></div>
    </div>
    <div class="layout-content">
    <Row type="flex">
      <Col span="5" class="layout-menu-left">
      <Menu active-name="1-1" theme="dark" width="auto" :open-names="['1']">
        <Menu-group title="欢迎你，图书管理员！">
          {{msg}}
        <Submenu name="1">
          <template slot="title">
            <Icon type="ios-navigate"></Icon>
            后台管理
          </template>
          <MenuItem name="1-1" @click.native="lookUser"><span>
            <Icon type="ios-person"></Icon>
            用户管理</span>
          </MenuItem>
          <MenuItem name="1-2" @click.native="lookBook"><span>
            <Icon type="ios-book"></Icon>
            图书管理</span>
          </MenuItem>
          <MenuItem name="1-3" @click.native="lookRecord"><span>
            <Icon type="ios-keypad"></Icon>
            借阅记录</span>
          </MenuItem>
          <MenuItem name="1-4" @click.native="Verify"><span>
           <Icon type="ios-flag"></Icon>
            用户审核</span>
          </MenuItem>
          <MenuItem name="1-5" @click.native="Feedback"><span>
           <Icon type="ios-paper"></Icon>
            用户留言</span>
          </MenuItem>
        </Submenu>>
        </Menu-group>
        <!--<Submenu name="3">
          <template slot="title">
            <Icon type="ios-analytics"></Icon>
            网站管理
          </template>
          <MenuItem name="3-1">Option 1</MenuItem>
          <MenuItem name="3-2">Option 2</MenuItem>
        </Submenu>-->
      </Menu>
      </Col>
      <Col span="19">
      <div class="layout-breadcrumb">
        <Breadcrumb>
          <BreadcrumbItem href="#">{{one_nav}}</BreadcrumbItem>
          <BreadcrumbItem href="#">{{two_nav}}</BreadcrumbItem>
          <BreadcrumbItem>{{three_nav}}</BreadcrumbItem>
          <Button id="login_btn"  @click="warning(false)">
            <Badge dot><Icon type="ios-mail-outline"size="26" color:white></Icon></Badge></Button>
        </Breadcrumb>
      </div>
        <div class="layout-content-main">
          <template id="userManage"></template>
          <template id="bookManage"></template>
          <template id="recordManage"></template>
          <template id="verifyUser"></template>
          <template id="feedBack"></template>
          <component :is="currentView"></component>
        </div>
      <div style="height: 600px"></div>
      <div class="layout-copy">
        2019-2020 &copy; 图书馆管理
      </div>
      </Col>
    </Row>
  </div>
  </div>
</template>
<script>
  import userManage from '../components/UserManage.vue'
  import bookManage from '../components/BookManage.vue'
  import recordManage from '../components/RecordManage.vue'
  import verifyUser from '../components/VerifyUser.vue'
  import feedBack from '../components/Feedback.vue'
  import Button from 'iview/src/components/button/button'
  export default {
    name: 'Index',
    data () {
      return {
        msg: 'haha',
        one_nav: '主页',
        two_nav: '后台管理',
        three_nav: '用户管理',
        currentView: 'userManage',
        modal7: false,
        modal2: false,
        modal6: false,
        modal_loading: false,
        value:5.0,
      }
    },
    mounted () {
      this.msg = window.localStorage.getItem('username')
    },
    methods: {
      lookUser () {
        this.currentView = 'userManage'
        this.one_nav = '主页'
        this.two_nav = '后台管理'
        this.three_nav = '用户管理'
        this.currentView = 'userManage'
      },
      lookBook () {
        this.one_nav = '主页'
        this.two_nav = '后台管理'
        this.three_nav = '图书管理'
        this.currentView = 'bookManage'
      },
      lookRecord () {
        this.one_nav = '主页'
        this.two_nav = '后台管理'
        this.three_nav = '借阅记录'
        this.currentView = 'recordManage'
      },
      Verify(){
        this.currentView='verifyUser'
        this.one_nav = '主页'
        this.two_nav = '后台管理'
        this.three_nav = '用户审核'
      },
      Feedback(){
        this.currentView='feedBack'
        this.one_nav = '主页'
        this.two_nav = '后台管理'
        this.three_nav = '用户留言'
      },
      del () {
        this.modal_loading = true;
        setTimeout(() => {
          this.modal_loading = false;
          this.modal6 = false;
          this.$Message.success('退出成功');
          this.$Loading.start();
          this.$Loading.finish();
          this.$router.push('/login');
        }, 2000);
      },
      confirm () {
        this.modal_loading = true;
          this.modal_loading = false;
          this.modal7 = false;
      },
      ok () {
        this.$Message.info('上传成功！');
      },
      cancel () {
        this.$Message.info('取消上传');
      },
      changePage1(){
        this.$router.push('/login')
      }
    },
    components: {
      Button,
      userManage: userManage,
      bookManage: bookManage,
      recordManage: recordManage,
      verifyUser: verifyUser,
      feedBack:feedBack,
    }
  }
</script>
<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
    text-align:left;
  }
  .layout-content{
    min-height: 200px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .layout-content-main{
    padding: 10px;
  }
  .layout-copy{
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }
  .layout-ceiling{
    background: #464c5b;
    padding: 55px 0;
    overflow: hidden;
  }
  .layout-ceiling-main{
    float: right;
    margin-right: 15px;
    margin-top: 25px;
    margin-bottom:-90px;
  }
  .layout-ceiling-left{
    float: left;
    margin-right: 15px;
    margin-top: 5px;
    margin-bottom:-80px;
    width:200px;
  }
  .layout-ceiling-main a{
    color: #9ba7b5;
  }
  .layout-menu-left{
    background: #464c5b;
  }
  .layout-header{
    height: 5px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
  }

  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  #login_btn{
    width:10%;
    margin-left:805px;
  }
  Button{
    color: #0553ff;
  }
</style>



