<template>
  <div class="hello" :style ="note">
    <Form ref="formItem" :model="formItem" :rules="ruleItem" :label-width="80">
      <Row>
        <Col :xs="23" :sm="23" :md="23" :lg="23">
        <h2>{{msg}}</h2>
        </Col>
      </Row>
      <Row>
        <Col :xs="23" :sm="23" :md="23" :lg="23">
        <FormItem prop="username" label="昵称">
          <Input type="text" v-model="formItem.username" placeholder="填写昵称">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        </Col>
      </Row>
      <Row>
        <Col :xs="23" :sm="23" :md="23" :lg="23">
        <FormItem prop="account" label="账号">
          <Input type="text" v-model="formItem.account" placeholder="填写有效的学号/教职工号">
          <Icon type="ios-home-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        </Col>
      </Row>
      <Row>
        <Col :xs="23" :sm="23" :md="23" :lg="23">
        <FormItem prop="password" label="密码">
          <Input type="password" v-model="formItem.password" placeholder="你的密码">
          <Icon type="ios-lock-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        </Col>
      </Row>
      <Row>
        <Col :xs="23" :sm="23" :md="23" :lg="23">
        <FormItem prop="repassword" label="确认密码">
          <Input type="password" v-model="formItem.repassword" placeholder="再次输入以确认密码">
          <Icon type="ios-lock-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        </Col>
      </Row>
      <Row>
        <Col :xs="24" :sm="24" :md="24" :lg="24">
        <Button id="register_btn" shape="circle" type="primary" :loading="loading" @click.native="handleSubmit('formItem')">
          <span v-if="!loading">注册</span>
          <span v-else>注册中...</span>
        </Button>
        </Col>
      </Row>
      <Row class="tip">
        <Col :xs="10" :sm="10" :md="10" :lg="10" offset="16">
        <span class="login_font" >   <a @click="changePage()">已有账号？登录 </a> </span>
        </Col>
      </Row>
    </Form>

    <!-- <img src="../assets/1.png" style="width: auto; height: 120px;">-->
    <vue-particles
      color="#fff"
      :particleOpacity="0.7"
      :particlesNumber="60"
      shapeType="circle"
      :particleSize="4"
      linesColor="#fff"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
      class="lizi"
    >
    </vue-particles>
  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data () {
      return {
        note: {
          width: "100%",
          height: "100%",
          backgroundColor: "#e7e1e1",
          backgroundImage: 'url(' + require("../assets/1.png") + ')',
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center,center",
          backgroundSize: "cover",
          marginTop: "40px",
        },

        msg: '图书馆',
        verifyCode: 'hello',
        formItem: {
          username:'',
          account: '',
          password: '',
          repassword:'',
          code: ''
        },
        ruleItem: {
          username: [{
            required: true,
            message: '昵称不能为空',
            trigger: 'blur'
          }, {
              type: 'string',
              max: 30,
              message: '昵称不能超过30位',
              trigger: 'blur'
            }
          ],
          account: [{
            required: true,
            message: '请填写有效学号/教职工号！',
            trigger: 'blur'
          }
          ],
          password: [{
            required: true,
            message: '请填写有效密码!',
            trigger: 'blur'
          }, {
            type: 'string',
            min: 3,
            message: '密码长度不能小于6位',
            trigger: 'blur'
          }],
          repassword: [{
            required: true,
            message: '请再次输入密码！',
            trigger: 'blur'
          }, {
            type: 'string',
            min: 3,
            message: '密码长度不能小于6位',
            trigger: 'blur'
          },{
            validator:(rule,value,callback)=>{
              if(value===''){
                callback(new Error('请再次输入密码'))
              }else if(value!==this.formItem.password){
                callback(new Error('两次输入密码不一致'))
              }else{
                callback( )
              }
            },
            trigger:'blur'
          }]
        },
        loading: false
      }
    },

    methods: {
      handleSubmit (name) {
        var that = this
        this.loading = true
        this.$refs[name].validate((valid) => {
          if (valid) {
              //通过POST请求获取服务器上的数据
              that.$http.post(that.GLOBAL.serverPath + '/excise/register',
                {
                  username: that.formItem.name,
                  account: that.formItem.account,
                  password: that.formItem.password,
                },
                {
                  emulateJSON: true
                }
              ).then(function (res) {
                console.log(res.data.registerUser)
                if (res.data.result === 'yes') {
                  this.$Message.success('注册成功!')
                  window.localStorage.setItem('userId', res.data.registerUser.rid)
                  window.localStorage.setItem('account', res.data.registerUser.account)
                  window.localStorage.setItem('username', res.data.registerUser.name)
                  window.localStorage.setItem('sex', res.data.registerUser.sex)
                  window.localStorage.setItem('condi', res.data.registerUser.condi)
                  //console.log('hahaha' + res.data.condi)
                  this.$router.replace({path: '/login'})
                } else {
                  this.$Message.error('注册失败！账号或密码有误')
                  this.loading = false
                }
              })
          } else {
            this.loading = false
          }
        })
      },
      changePage() {
        setTimeout(() => {
          this.$Loading.start();
          this.$Loading.finish();
          this.$router.push({path: '/login'});
        }, 600);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello{
    margin: auto;
  }
  h2{
    color: #ffffff;
    margin-bottom:50px;
    margin-right:-40px;
  }
  Form{
    margin: auto;
    width:400px;
    border: 2px solid rgba(255, 255, 255, 0);
    padding:60px 0;
    /*background-color:red;*/
  }
  #register_btn{
    width:80%;
    margin-right:-50px;
  }
  .tip{
    margin-top:10px;
    color: #fbfffd;
  }
  .tip a{
    color: #ffffff;
  }
  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  #verifyCode{
  }
</style>



